import Vue from 'vue'
import App from './App.vue'
import router from './router'

Vue.config.productionTip = false

// import and setup name frame source 
import BaseFrame from './frames/BaseFrame.vue'
import MiniFrame from './frames/MiniFrame.vue'
import PostFrame from './frames/PostFrame.vue'

Vue.component('base-frame', BaseFrame)
Vue.component('mini-frame', MiniFrame)
Vue.component('post-frame', PostFrame)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
