<template>
  <div id="main-page">
      <header-component></header-component>
      <div id="content-main">
          <div class="container">
              <div class="row">
                  <div class="col-lg-8">
                      <slot></slot>
                  </div>
                  <div class="col-lg-4">
                      <categories-box-component class="animate__animated animate__fadeInRight" :categories_list="categories"></categories-box-component>
                      <post-box-component class="animate__animated animate__fadeInRight"></post-box-component>
                      <post-box-component class="animate__animated animate__fadeInRight"></post-box-component>
                  </div>
              </div>
          </div>
          <footer-component></footer-component>
      </div>
  </div>
</template>

<script>
import {ServiceFactory} from '../base/ServiceFactory'

import HeaderComponent from '../components/common/HeaderComponent.vue'
import FooterComponent from '../components/common/FooterComponent.vue'
import CategoriesBoxComponent from '../components/boxs/CategoriesBoxComponent.vue'
import PostBoxComponent from '../components/boxs/PostBoxComponent.vue'

const CategoryRequest = ServiceFactory.get('category')

export default {
    name: 'base-frame',
    components: {
        'header-component': HeaderComponent,
        'footer-component': FooterComponent,
        'categories-box-component': CategoriesBoxComponent,
        'post-box-component': PostBoxComponent
    },
    data () {
        return {
            categories: {}
        }
    },
    created() {
        this.fetch()
    },
    methods: {
        async fetch() {
            const data_categories  = await CategoryRequest.get();
            this.categories = data_categories.data;
        }
    }
}
</script>