<template>
  <component :is="frameName">
    <router-view :key="$route.fullPath" />
  </component>
</template>

<script>
const defaultFrameName = 'base';

export default {
  name: 'App',
  computed: {
    frameName() {
      return ( this.$route.meta.frame || defaultFrameName ) + "-frame";
    }
  }
}
</script>